.input-block {
  max-width: 730px;
  height: 49px;
  position: relative;
  border-radius: 4px;

  > .input {
    width: 100%;
    height: 49px;
    box-sizing: border-box;
    border: none;
    border-bottom: 2px solid #D3D3D3;
    outline: 0;
    outline-offset: 0;

    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    padding-top: 25px;
    color: #3b3c44;
    transition: border-bottom 0.1s ease-in-out;

    &.active:focus {
      border: none;
      border-bottom: 2px solid #2653af;
      outline: 0;
      outline-offset: 0;
    }

    &.disabled:hover {
      cursor: not-allowed;
    }

  }

  .title {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;

    color: #787a87;
    position: absolute;
    top: 0;
    left: 2px;
  }

  &.inputError {
    input {
      border-bottom: 2px solid #ff4255;
    }

    animation: shakeX 0.82s cubic-bezier(.35, .08, .2, .95) both;
    transform: translateX(0);
  }

  @keyframes shakeX {
    10%, 90% {
      transform: translateX(-1px);
    }

    20%, 80% {
      transform: translateX(2px);
    }

    30%, 50%, 70% {
      transform: translateX(-4px);
    }

    40%, 60% {
      transform: translateX(4px);
    }
  }


  .text-error {
    display: none;
  }

  &.inputError .text-error {
    display: unset;
    margin-top: 5px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #ff4255;
  }

  .error-icon {
    display: none;
  }

  &.inputError .error-icon {
    display: block;
    position: absolute;
    width: 30px;
    height: 30px;
    right: 10px;
    top: 20px;
    background: url("./error.svg");
    border-radius: 1px;
  }
}
